        left: 0;
    }
    .swiper-next {
        position: absolute;
        top: 30px;
        right: 0;
    }
    
    
    .swiper-pagination {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: auto;
    }
    .swiper-slide {
        transition: opacity 0.4s;
        opacity: 0;
    }
    .swiper-slide-visible {
        opacity: 1;
    }
    @include breakpoint(lg) {
        padding: 0 40px 100px;
       
        .swiper-pagination {
            bottom: 0;
        }
        .swiper-prev,
        .swiper-next {
            top: 80px;
        }
        .swiper-prev {
            left: 0;
        }
        .swiper-next {
            right: 0;
        }
    }
}


// -----------

.swiper-gallery {

}

.swiper-gallery-slides {
    margin: 0 -16px 36px;
    position: relative;
    overflow: visible;
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 16px;
        left: 16px;
        right: 16px;
        bottom: -16px;
        border: 1px solid #444444;
        z-index: 100;
        pointer-events: none;
    }
    picture {
        display: block;
        overflow: hidden;
        position: relative;
        &:before {
            content: '';
            float: left;
            padding: 85% 0 0 0;
        }
        img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .swiper-pagination {
        position: absolute;
        bottom: 16px;
        left: 32px;
    }
    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        margin: 0 5px 0 0;
        border-color: #F0F1EC;
    }
    .swiper-pagination-bullet-active {
        border-color: #F0F1EC;
        background: #F0F1EC;
    }

    .swiper-prev {
        display: none;
    }
    .swiper-next {
        position: absolute;
        right: 32px;
        bottom: 16px;
    }

    @include breakpoint(lg) {
        margin: 0 0 20px;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 50%;
            width: 120vw;
            margin: 0 0 0 -60vw;
            z-index: -1;
            background: #DAD9D3;
        }
        &:after {
            top: -20px;
            left: -20px;
            right: -20px;
            bottom: 20px;
        }

        picture {
            height: 540px;
        }

        .swiper-prev, .swiper-next {
            display: block;
            position: absolute;
            top: 50%;
            bottom: auto;
            margin: -23px 0 0 0;
        }
        .swiper-prev {
            left: 40px;
        }
        .swiper-next {
            right: 40px;
        }

        .swiper-pagination {
            position: absolute;
            left: auto;
            bottom: 40px;
            right: 88px;
        }
    }
}

.swiper-gallery-thumbs {
    overflow: visible;
    margin: 0 0 30px;
    .swiper-slide {
        width: auto;
    }
    
    picture {
        display: block;
        width: 120px;
        height: 80px;
        background: #444;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    @include breakpoint(lg) {
        overflow: hidden;
        margin: 0;
        max-width: 520px;
        picture {
            width: auto;
            height: 100px;
        }
    }
}


.swiper-gallery-caption {
    display: block;
    margin: 0;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
    letter-spacing: 0.06em;
    @include breakpoint(lg) {
        margin: 0 0 0 8px;
    }
}